<template><section><h1>Tree 折叠框</h1>
<hr>
<h3>概述</h3>
<p>Tree 折叠框组件，依赖vux组件库，使用时应在本组件外包裹 Group 组件，
依赖 QTreeLeaf组件，应该与QTreeLeaf组件一起使用</p>
<h3>基础用法（多选）</h3>

<div class="demo-block">
  <Group>
    <QTree :data="treeData" @changeLeaf="changeLeaf" @clickLeaf="clickLeaf"></QTree>
  </Group>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body>import { Group } from 'vux'

export default {
  components: {
    Group
  }
}

<group>
  <qtree :data="treeData" @changeleaf="changeLeaf" @clickleaf="clickLeaf"></qtree>
</group>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">import { Group } from 'vux'

export default {
  components: {
    Group
  }
}

<span class="hljs-tag">&lt;<span class="hljs-name">Group</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">QTree</span>
    <span class="hljs-attr">:data</span> = <span class="hljs-string">"treeData"</span>
    <span class="hljs-attr">@changeLeaf</span> = <span class="hljs-string">"changeLeaf"</span>
    <span class="hljs-attr">@clickLeaf</span> = <span class="hljs-string">"clickLeaf"</span>
  &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">QTree</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">Group</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">treeData</span>: [{
          <span class="hljs-attr">title</span>: <span class="hljs-string">'高一一部'</span>,
          <span class="hljs-attr">islink</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>,
          <span class="hljs-attr">checked</span>: <span class="hljs-literal">false</span>,
          <span class="hljs-attr">childData</span>: [{
              <span class="hljs-attr">title</span>: <span class="hljs-string">'高15级1班'</span>,
              <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>,
              <span class="hljs-attr">checked</span>: <span class="hljs-literal">false</span>,
            },{
              <span class="hljs-attr">title</span>: <span class="hljs-string">'高15级2班'</span>,
              <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>,
              <span class="hljs-attr">checked</span>: <span class="hljs-literal">false</span>,
            }],
        }]
      }
    },
    <span class="hljs-attr">methods</span>: {
      changeLeaf(leafData){
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'折叠状态切换'</span>, leafData)
      },
      clickLeaf(leafData){
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'选择状态切换'</span>, leafData)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>单选结构</h3>
<div class="demo-block">
  <Group>
    <QTree :data="treeData2" @changeLeaf="changeLeaf" @clickLeaf="clickLeaf"></QTree>
  </Group>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body>import { Group } from 'vux'

