</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>树状数据，子数据格式参照QTreeLeaf组件</td>
<td>json</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h3>Event</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>changeLeaf</td>
<td>切换树状结构打开状态，返回操作后的数据</td>
<td></td>
</tr>
<tr>
<td>clickLeaf</td>
<td>切换树状结构节点选中状态，返回操作后的数据</td>
<td></td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default{
    data () {
      return {
        treeData: [{
          title: '高一一部',
          islink: true,
          open: false,
          checked: false,
          childData: [{
              title: '高15级1班',
              open: false,
              checked: false,
            },{
              title: '高15级2班',
              open: false,
              checked: false,
            }],
        }],
        treeData2: [{
          title: '高一一部',
          islink: true,
          open: false,
          childData: [{
            title: '高15级1班',
            islink: true,
            link: 'http://www.baidu.com'
          },{
            title: '高15级2班',
            islink: true,
            link: 'http://www.baidu.com'
          }],
        }]
      }
    },
    methods: {
      changeLeaf(leafData){
        console.log('折叠状态切换', leafData)
      },
      clickLeaf(leafData){
        console.log('选择状态切换', leafData)
      }
    }
  }
</script>


